import Header from "../components/headers"
import {GetcontactAction} from "../actions/contactbooActions"
import { useDispatch, useSelector } from "react-redux"
import { useEffect } from "react"
import { useHistory } from "react-router-dom"
const ViewContacts=()=>{


    const dispatch = useDispatch()
    useEffect(()=>{
        dispatch(GetcontactAction())
    },[])

const ViewContacts = useSelector((store)=>store.showcontacts)
const {error,response,loading}=ViewContacts



useEffect(()=>{

},[error,response,loading])
const history = useHistory()
const Addcontact =()=>{
history.push('/addcontact')
}


    return(
        <div className="contactlist">
            <Header title="Contact list"/>


<table className="table table-stripped">
    <thead>
        <tr >
            <td className="table-primary">id</td> 
            <td className="table-secondary">First name</td>
            <td className="table-secondary">Middle name</td>
            <td className="table-secondary">Last Name</td>
            <td class="table-success">Email</td>
            <td class="table-success">Phone</td>
            <td class="table-success">Alternative Phone</td>
            <td class="table-success">Address</td>
        </tr>
    </thead>
    <tbody>
        {response &&
        response.data &&
        response.data.data &&
        response.data.data.length > 0 &&
        response.data.data.map((d)=>{
            return(
                <tr>
                    <td>{d.id}</td>
                    <td>{d.fname}</td>
                    <td>{d.mname}</td>
                    <td>{d.lname}</td>
                    <td>{d.email}</td>
                    <td>{d.phone}</td>
                    <td>{d.alterphone}</td>
                    <td>{d.address}</td>
                </tr>
            )
        })
        }
    </tbody>
</table>
<button className="boxer btn btn-info" onClick={Addcontact}>Add Contact</button>
        </div>
    )
}


export default ViewContacts