import cypher from "../logos/cypher.jpg"
import signupbg from "../logos/navimg.jpg"

const { Link } = require("react-router-dom")

const Navigation = (props)=>{
    return(
      <div >
        
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
  <div style={{ 
        backgroundImage: `url("${signupbg}")`,
        backgroundRepeat:' no-repeat center center fixed; ',         
      }} className="respimgbg" className="container-fluid">
     <Link to="/home" className="navbar-brand"><img src={cypher} style={{height:'30px',width:'30px'}}></img>Cypher</Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
      
    <li className="nav-item">
      <Link to="/viewusers" className="nav-link">View user</Link>
    </li>
     
        
      </ul>
    
    </div>
  </div>
</nav>
            </div>
    )
     }
    
    
    
     export default Navigation