import {SigninAction} from '../actions/useractions'
import { useDispatch, useSelector } from 'react-redux'
import { useEffect, useState } from 'react'
import {useHistory} from "react-router-dom"
import signupbg from "../logos/cyphert.jpg"

const { default: Header } = require("../components/headers")


const SigninScreen = (props)=>{
  const history = useHistory()
  const onSignuppush=()=>{
   history.push('/signup')
  }
const [email,setEmail]=useState('')
const [password,setPassword]=useState('')

const singnindatafromserver= useSelector((store)=> store.signin)
const {error,response,loading}=singnindatafromserver


const dispatch = useDispatch()

const Signin =()=>{
  dispatch(SigninAction(email,password))

}

useEffect(() => {
  if (response && response.status === 'success') {
    sessionStorage.setItem('token', response.data.token)
    history.push('/dashboard')
  } else if (response && response.status === 'error') {
    alert(response.error)
  } else if (error) {
    alert(error)
  }
}, [loading, error, response])



    return(
    <div className="container">
              <div style={{ 
        backgroundImage: `url("${signupbg}")`,
        backgroundRepeat:' no-repeat center center fixed; ',         
      }} className="respimgbg">

<div className="row">
    
  <div className="col-md-8 offset-md-2">
    <div >
    <Header title="Sign In"></Header>

        <div className="Names">
          <label >Email address</label>
          <input onChange={(e)=>{
            setEmail(e.target.value)
          }} type="email" className="boxer boxer form-control"   placeholder="Enter email"/>
          <small  className="form-text text-muted"></small>
        </div>
        <div className="Names">
          <label >Password</label>
          <input onChange={(e)=>{setPassword(e.target.value)}} type="password" className="boxer boxer form-control" id="exampleInputPassword1" placeholder="Password"/>
        </div>
        <div className="Names form-check">
          <input type="checkbox" className="form-check-input" />
          <label className="form-check-label" >Check me out</label>
        </div>
        <button onClick={Signin} type="submit" className="btn btn-primary">Sign In</button>

        <div className="float-end">          <button onClick={onSignuppush} type="submit" className="btn btn-success float-end">Sign Up</button>

<span className="already">     Have Account?? Click</span>
</div>
    </div>
  </div>
</div>
</div>
{loading && <div>waiting for response</div>}
        </div>
    )
     }
    
    
    
     export default SigninScreen