import {signup} from '../actions/useractions'
import  Validator from 'validator'
import { useEffect, useState} from 'react'
import {useDispatch, useSelector} from 'react-redux'
import {useHistory} from "react-router-dom"
import signupbg from "../logos/cyphert.jpg"

import { AlphabetValidate, BetweenFixLengthNumbers, CompareNumbers, fixedLengthNumbers, FixLengthNumbers, NumberValidate, validate } from './utils'
const { default: Header } = require("../components/headers")

const SignupScreen = (props)=>{


    const [firstname,setFirstname]=useState('');
    const [lastname,setLastname]=useState('');
    const [email,setEmail]=useState('');
    const [phone,setPhone]=useState('');
    const [address,setAddress]=useState('');
    const [password,setPassword]=useState('');
    const [confirmpasswor,setConfirmPassword]=useState('');
const dispatch = useDispatch()

const onSignup = ()=>{
  if(validate('firstname','firstnameDiv','First name is mandatory....') &&
     AlphabetValidate('firstname','firstnameDiv','No person in world whos First name has numbers....')&&
     AlphabetValidate('lastname','lastnameDiv','No person in world whos last name has numbers...!')&&
      validate('lastname','lastnameDiv','Last naem is mandatory...!')&&
      validate('email','emailDiv','Email is mandatory...!')&&
      validate('phone','phoneDiv','Phone is manadatory')&&
      NumberValidate('phone','phoneDiv','Your phone will never ring with alphabets or specials..')&&
      fixedLengthNumbers(phone,'phoneDiv','Only 10 Digit Numbers Allowd..')&&
      validate('address','addressDiv','Address is required')&&
      validate('password','passwordDiv','Enter password')&&
      validate('confirmpassword','confirmpasswordDiv','Enter password')&&
      CompareNumbers('password','confirmpassword','passwordDiv','Password not Matching')&&
     BetweenFixLengthNumbers(password,'passwordDiv','Password length should between 6 to 15')
   ){
  
  const emailverify = document.getElementById('emailDiv')
  if (!Validator.isEmail(email))
   {
     emailverify.innerHTML = 'enter valid email'
     return true
 
   } 
       
   dispatch(signup(firstname,lastname,email,phone,address,password))
    
  }
 
  
}
const signupdata = useSelector((store)=>store.signup)
const {error,response,loading}=signupdata
useEffect(() => {
  if (response && response.data.status === 'success') {
    sessionStorage.setItem('token', response.data.token)
    props.history.push('/signin')
  } else if (response && response.status === 'error') {
    alert(response.error)
  } else if (error) {
    alert(error)
  }
}, [loading, error, response])

const history = useHistory()
const onSignin=()=>{
history.push('/signin')
}

    return(
        <div style={{ 
          backgroundImage: `url("${signupbg}")`,
          backgroundRepeat:' no-repeat center center fixed; ',         
        }} className="respimgbg">
          
    <div className="container">

      <div className="row">
          
        <div className="col-md-8 offset-md-2">
          <div >
          <Header title="Sign Up"></Header>
          <div>
                <label className="Names"  >First Name</label>
                <input id="firstname" onChange={(e)=>{
                    setFirstname(e.target.value)
                }} type="text" className="boxer form-control"   placeholder="Enter Your First Name"/>
                <div id="firstnameDiv"  className="errordiv"></div>
              </div>
              <div className="form-group">
                <label className="Names" >Last Name</label>
                <input id="lastname" onChange={(e)=>{
                    setLastname(e.target.value)
                }} type="text" className="boxer form-control"   placeholder="Enter Your Last Name"/>
                <div className="errordiv" id="lastnameDiv"  className="errordiv"></div>
              </div>
              <div className="form-group">
                <label className="Names" >Email address</label>
                <input id="email" onChange={(e)=>{
                    setEmail(e.target.value)
                }} type="email" className="boxer form-control"   placeholder="Enter email"/>
                <div className="errordiv" id="emailDiv"  className="errordiv"></div>
              </div>
              <div className="form-group">
                <label className="Names" >Phone</label>
                <input id="phone" onChange={(e)=>{
                    setPhone(e.target.value)
                }} type="phone" className="boxer form-control"   placeholder="Enter Your Phone number with country code"/>
                <div  className="errordiv" id="phoneDiv" className="errordiv"></div>
              </div>
              <div className="form-group">
                <label className="Names" >Address</label>
                <input id="address" onChange={(e)=>{
                    setAddress(e.target.value)
                }} type="text" className="boxer form-control"   placeholder="Enter Your Address : city/town, tal, dist, state, country "/>
                <div className="errordiv" id="addressDiv" className="errordiv"></div>
              </div>
              <div className="form-group">
                <label className="Names" >Password</label>
                <input id="password" onChange={(e)=>{
                    setPassword(e.target.value)
                }} type="password" className="boxer form-control" placeholder="Password"/> 
           <div className="errordiv" id="passwordDiv"  className="errordiv"></div>
              </div>
              <div className="form-group">
                <label className="Names" >Confirm Password</label>
                <input id="confirmpassword" onChange={(e)=>{
                    setConfirmPassword(e.target.value)
                }} type="password" className="boxer form-control"  placeholder="Password"/>
           <div className="errordiv" id="confirmpasswordDiv"  className="errordiv"></div>
           <div className="errordiv" id="passwordverify"></div>
              </div>
              <div className="form-group form-check">
                <input type="checkbox" className="form-check-input" />
                <label className="Names" className="form-check-label" >Check me out</label>
              </div>
              <button onClick={onSignup} type="submit" className="btn btn-primary">Submit</button>
              <button onClick={onSignin} className="btn btn-success float-end">Signin</button>
         <div className="float-end">  <span className="already">      Already have account!!!?</span>
</div>
          </div>
        </div>
      </div>
    </div>

    
  
 
  
</div>





       
    )
     }
    
    
    
     export default SignupScreen