import signupbg from "../logos/cyphert.jpg"

const { default: Header } = require("../components/headers")

 const HomeScreen = (props)=>{

    const signup = ()=>{
props.history.push('./signup')
    }

    const signin = ()=>{
props.history.push('./signin')
    }
return(
    <div style={{ 
        backgroundImage: `url("${signupbg}")`,
        backgroundRepeat:' no-repeat center center fixed; ',         
      }} className="respimgbg">

        <Header title="Home"></Header>
        

   <div className="homediv">
   <button onClick={signup} className="but  btn btn-primary">Sign Up</button>
    <button onClick={signin} className="but  btn-success">Sign In</button>
       </div>         
    
  </div>

    )
 }



 export default HomeScreen